import {Observable, of as observableOf} from 'rxjs';
import {DmsObjectTarget} from '../../action-target';
import {Component} from '@angular/core';
import {SelectionRange} from '../../selection-range.enum';
import {TranslateService, DmsObject, SigningService} from '@eo-sdk/core';
import {ComponentAction} from '../../interfaces/action.interface';
import {CancelSigningComponent} from './cancel-signing/cancel-signing.component';

@Component({
  selector: 'eo-cancel-signing',
  template: ``
})
export class CancelSigningActionComponent extends DmsObjectTarget implements ComponentAction {
  header: string;
  label: string;
  description: string;
  priority = 2;
  iconSrc = 'assets/_default/svg/ic_signature.svg';
  group = 'common';
  range = SelectionRange.SINGLE_SELECT;
  component = CancelSigningComponent;

  constructor(private translate: TranslateService, private signingService: SigningService) {
    super();
    this.label = this.translate.instant('eo.action.cancel.signing.label');
    this.description = this.translate.instant('eo.action.cancel.signing.description');
  }

  isExecutable(element: DmsObject): Observable<boolean> {
    return observableOf(this.signingService.signingInProcess(element)
      && this.hasEditRights(element)
      && !this.isFinalized(element));
  }

  hasEditRights(item: DmsObject): boolean {
    return item.rights.edit;
  }

  isFinalized(item: DmsObject): boolean {
    return item.isFinalized;
  }
}
