
import {of as observableOf} from 'rxjs';
import {DmsObjectTarget} from '../../action-target';
import {ComponentAction} from '../../interfaces/action.interface';
import {Component} from '@angular/core';
import {SelectionRange} from '../../selection-range.enum';
import {TranslateService} from '@eo-sdk/core';
import {DmsObject} from '@eo-sdk/core';
import {DownloadObjectActionComponent} from './download-object-action/download-object-action.component';

@Component({
  selector: 'eo-download-content',
  template: ``
})
export class DownloadActionComponent extends DmsObjectTarget implements  ComponentAction {
  header: string;
  label: string;
  description: string;
  priority = 2;
  iconSrc = 'assets/_default/svg/ic_content-download.svg';
  group = 'common';
  range = SelectionRange.MULTI_SELECT;
  component = DownloadObjectActionComponent;

  constructor(private translate: TranslateService) {
    super();
    this.label = this.translate.instant('eo.action.download.dms.object.content.label');
    this.description = this.translate.instant('eo.action.download.dms.object.content.description');
    this.header = this.translate.instant('eo.export.title');
  }

  isExecutable(element: DmsObject) {
    return observableOf(!!element.content && !!element.content.id);
  }
}
