import {Component, OnDestroy, Input, HostBinding, OnInit} from '@angular/core';
import {Subscription} from 'rxjs';
import {EnaioEvent, EventService, UserService} from '@eo-sdk/core';

@Component({
  selector: 'eo-user-avatar',
  template: `<span class="presence" *ngIf="showPresence && !present">X</span>`,
  styleUrls: ['./user-avatar.component.scss']
})
export class UserAvatarComponent implements OnDestroy, OnInit {

  private subscription: Subscription;
  // tslint:disable-next-line:max-line-length
  private defImg = 'data:image/png;base64,';

  present: boolean;
  userId: string;
  @HostBinding('style.background-image') backgroundImage = 'initial';
  @Input() showPresence: boolean;
  @Input() previewUri: string;

  constructor(private userService: UserService,
    private eventService: EventService,) {
    this.subscription = this.userService.user$
      .subscribe(u => {
        this.userId = u.id;
        this.present = u.present;
        this.refreshUserImage();
      });
    this.eventService.on(EnaioEvent.SYSTEM_STATUS_IMAGE_CHANGE).subscribe(() => {
      this.refreshUserImage();
    });
  }

  ngOnInit(): void {
    if (this.previewUri) {
      this.refreshUserImage();
    }
  }

  refreshUserImage() {
    if (this.previewUri) {
      this.backgroundImage = `url(${this.previewUri})`;
    } else {
      const timestamp = new Date().getTime().toString();
      this.backgroundImage = `url(${this.userService.getUserImageUri(this.userId) + '&t=' + timestamp}), url(${this.defImg})`;
    }
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
